<script>
import { mapState } from 'vuex';
export default {
    data() {
        return {
            time: null
        }
    },
    watch: {
        ifisshowtanchaung(newVal) {
            if (newVal) {
                this.startTimer();
            }
        }
    },
    methods: {
        // 启动倒计时器
        startTimer() {
            this.clearTimer();
            this.timer = setTimeout(() => {
                this.$store.commit("tanchuangshow", false)
            }, 2000);
        },

        // 清理定时器
        clearTimer() {
            if (this.timer) {
                clearTimeout(this.timer);
                this.timer = null;
            }
        }
    },
    computed: {
        ...mapState(['ifisshowtanchaung']),
    },
    beforeUnmount() {
        this.clearTimer();
    }
};
