<script>
import store from '@/store';
import { mapState } from 'vuex';
export default {
    data() {
        return {
            textlong: localStorage.getItem("textlonglong") == "true" ? true : false,
        }
    },
    methods: {
        // 退出登录
        isdenglu() {
            try {
                store.commit("ifisgongzuotaishow", false)
                if (localStorage.getItem("denglutoken")) {
                    localStorage.clear()
                    this.$store.commit("dlcg", "登录");
                }
            } catch (error) {
                console.log(error)
            }
        },
        //头像跳转
        touxiangpath() {
            try {
                this.$router.push({ name: "PersonageActivity" })
            } catch (error) {
                console.log(error)
            }

        }
    },
    computed: {
        ...mapState(['vuexuserarr'])
    },

    watch: {
        vuexuserarr() {
            try {
                this.$nextTick(() => {
                    const a = document.querySelector('.yonghuname').clientWidth
                    if (a >= 126) {
                        localStorage.setItem("textlonglong", true)
                        this.textlong = true
                    } else {
                        this.textlong = false
                    }
                });
            } catch (error) {
                console.log(error)
            }

        },
    }
};
</script>


