import { createRouter, createWebHistory } from "vue-router";
import loadXMLDoc from "@/store/AjaxRequest";

const routes = [
  //默认
  {
    path: "/",
    redirect: "/FunctiontWorkbench/UserLogin",
  },
  //工作台
  {
    path: "/FunctiontWorkbench",
    name: "FunctiontWorkbench",
    component: () => import("../views/Functiont/FunctiontWorkbench.vue"),
    children: [
      {
        path: "UserLogin",
        name: "UserLogin",
        component: () => import("../views/User/UserLogin.vue"),
      },
      // 注册
      {
        path: "UserRegistration",
        name: "UserRegistration",
        component: () => import("../views/User/UserRegistration.vue"),
      },
      // 协议
      {
        path: "/UserAgreement",
        name: "UserAgreement",
        component: () => import("../views/User/UserAgreement.vue"),
      },
    ],
  },

  // 活动管理
  {
    path: "/FunctiontActivity",
    name: "FunctiontActivity",
    component: () => import("../views/Functiont/FunctiontActivity.vue"),
  },
  //积分中心
  {
    path: "/FunctiontIntegral",
    name: "FunctiontIntegral",
    component: () => import("../views/Functiont/FunctiontIntegral.vue"),
  },
  //服务证书
  {
    path: "/PersonageServe",
    name: "PersonageServe",
    component: () => import("../views/Personage/PersonageServe.vue"),
  },
  //账号设置
  {
    path: "/PersonageActivity",
    name: "PersonageActivity",
    component: () => import("../views/Personage/PersonageActivity.vue"),
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});
//工作台
router.beforeEach((to, from, next) => {
  if (to.path == "/FunctiontWorkbench") {
    if (localStorage.getItem("token") == "undefined" || localStorage.getItem("token") == "null") {
      this.$store.commit("tanchaung", "登录已过期，请重新登录");
      this.$store.commit("tanchuangshow", true);
      localStorage.clear();
      return;
    }
  }
  next();
});
//登录
router.beforeEach((to, from, next) => {
  if (to.path == "/FunctiontWorkbench/UserLogin") {
    if (localStorage.getItem("token") == "undefined" || localStorage.getItem("token") == "null") {
      this.$store.commit("tanchaung", "登录已过期，请重新登录");
      this.$store.commit("tanchuangshow", true);
      localStorage.clear();
      return;
    }
  }
  next();
});

router.beforeEach((to, from, next) => {
  if (to.path == "/FunctiontActivity") {
    if (!localStorage.getItem("activity_type_config")) {
      loadXMLDoc({
        reqUrl: "http://8.148.31.153:3030/demo",
        callback(error, resdata) {
          try {
            console.log(resdata);
            if (!error) {
              const data = JSON.parse(resdata).resData;
              localStorage.setItem("activity_type_config", JSON.stringify(data));
            }
          } catch (error) {
            console.log(error);
          }
        },
      });
    }
  }
  next();
  return false;
});

export default router;
